
import { useContext, useEffect, useState } from 'react';
import '../App.css';
import Bio from '../Bio/Bio';
import Contacts from '../Contacts/Contacts';
import Main from '../Main/Main';
import React from 'react';
import Context from '../Context/Context';
import getUsers from '../hooks/getUsers';
import TreadHeader from '../Main/Header/TreadHeader';
import Poster from '../Main/Poster/Poster';
import TreadsBlock from '../Main/TreadsBl/TreadsBlock';
import HeaderBlock from '../Main/Header/TreadsBlockHeader';




function TreadsBl() {
const {sizes, screenWidth, setCurrentUser, setMobileBio,setCurrentPerson, setIsTrade, setIsPosts, setPostsDate, setIsContacts, setCurrentItem, setUsersDate , currentUser, mobileBio, currentPerson, isTrade, isPosts, postsDate, isContacts, currentItem, usersDate, setLocation, isReg, setIsReg} = useContext(Context)



  return (
    <>
      <Context.Provider value={{currentUser, usersDate, isContacts, setIsContacts, currentItem, setCurrentPerson, setMobileBio, screenWidth}}>
        <Contacts/>
      </Context.Provider>


      <Context.Provider value={{postsDate, isPosts, currentUser, setPostsDate, setIsContacts, setCurrentPerson, usersDate, isTrade, currentPerson, setUsersDate, setIsTrade, screenWidth}}>
      <div className="news">
        <div className="container_news">
            <Context.Provider value={{setIsContacts, currentPerson, setIsTrade, setUsersDate}}>
              <HeaderBlock/>
            </Context.Provider>
            <Context.Provider value={{postsDate, isPosts, setCurrentPerson, usersDate, currentUser, currentPerson, setIsTrade}}>
              <TreadsBlock/>
            </Context.Provider>
         
        </div>
    </div>
      </Context.Provider>


      {screenWidth > 500 ?<Context.Provider value={{setCurrentUser, currentUser, setUsersDate, usersDate, setPostsDate, setIsPosts, setCurrentItem, currentItem, currentPerson, setIsContacts, setIsTrade, mobileBio, setMobileBio, setCurrentPerson, screenWidth}}>
        <Bio usersDate={usersDate}/>
      </Context.Provider>: ''}
          </>
  );
}

export default TreadsBl;
