import { useContext, useRef } from "react";
import Context from "../../Context/Context";
import setPosts from "../../hooks/setPosts";
import setUsers from "../../hooks/setUsers";


function Poster(){
const monthNames = ["January", "February", "March", "April", "May", "June",
    "July", "August", "September", "October", "November", "December"
];
const weekNames = ['Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday', 'Sunday'];
const {currentUser, setPostsDate, postsDate, isTrade, currentPerson, usersDate, setUsersDate} = useContext(Context)
const message = useRef()
let newPostsDate = [...postsDate]

function getCurrentDate() {
    let da = new Date()
    let month = monthNames[da.getMonth()];
    let week = weekNames[((da.getDay() + 6) % 7)];
    let day = da.getDate();
    return `${week}, ${month}, ${day}`
}

function createCollectPost(time, content) {
    return {
        imgUrl: currentUser.img,
        postName: currentUser.name,
        postDate: time,
        postContent: content,
        id: currentUser.id
    }
}

function getCurrentTime() {
    let time = (function () {
        let day = new Date()
        return `${day.getHours()}:${day.getMinutes()}`;
    }())
    return time
}

async function collect(){
    let content = message.current.value
        let time = getCurrentTime()
        let date = getCurrentDate()
        let post = createCollectPost(time, content)
    if(currentUser && !isTrade){
        
        let isDate = [];
            newPostsDate.forEach(item => {
                isDate.push(item[0] === date)
                if (item[0] === date) {
                    item.push(post)
                    return true
                }
            })

            if (!isDate.includes(true)) {
                if (content !== 'clear') {
                    newPostsDate.push([date])
                    newPostsDate[newPostsDate.length - 1].push(post)
                }
            }

            setPostsDate(newPostsDate)
            setPosts(newPostsDate)
            
    }
    else if(isTrade){
        
        let usersClone = [...usersDate]

        if(await usersDate[usersDate.indexOf(currentUser)]){
            if(!currentUser.treads[currentPerson.id]){
                usersClone[usersClone.indexOf(currentUser)].treads[currentPerson.id] = []
                usersClone[usersClone.indexOf(currentPerson)].treads[currentUser.id] = []
                currentUser.treads[currentPerson.id] = []
                currentPerson.treads[currentUser.id] = []
            }
                     

        }
        
        if (!await usersDate[usersDate.indexOf(currentUser)].treads[currentPerson.id].includes(date)) {
            await usersClone[usersClone.indexOf(currentUser)].treads[currentPerson.id].push(date)
            await usersClone[usersClone.indexOf(currentPerson)].treads[currentUser.id].push(date)
        }
        await usersClone[usersClone.indexOf(currentUser)].treads[currentPerson.id].push(post);
        await usersClone[usersClone.indexOf(currentPerson)].treads[currentUser.id].push(post)
        setUsersDate(await usersClone)
        setUsers(usersClone)
    }
    message.current.value = ''
}

    return(
        <div className="message_in">
    <div className="block">
        <div className="message_left">
            <input ref={message} type="text" className="message_input" placeholder="Message in..."/>
        </div>
        <div className="message_right">
            <i className="fas message_send fa-circle-notch" onClick={()=> collect()}></i>
            </div>
        </div>
    </div>
    )
}
export default Poster