import { useContext, useEffect, useRef } from "react"
import Context from "../../../Context/Context"
import React from "react"
import getPosts from "../../../hooks/getPosts"
import move from "../../../hooks/move"
import { Link } from "react-router-dom"

function MobileLogin(){
    let email = useRef()
    let pass = useRef()
    let closer = useRef()
    const hideBlock = useRef()
    
    const {setRegister, setCurrentUser, usersDate, setPostsDate, setIsReg, iteme, register, setIsPosts, setCurrentItem, currentItem} = useContext(Context)
    useEffect(()=>{
        move(register, iteme, hideBlock)
        setCurrentItem({hiden: hideBlock, loging: iteme})
        
    }, [iteme])

    async function Reg(){
        usersDate.forEach(item=>{
            if(email.current.value == item.email && pass.current.value == item.password){
                    setCurrentUser(item)
                    move(true, iteme, hideBlock)
                    setIsReg(true)
                    getPosts(setPostsDate, setIsPosts)
                    

            }
        })
    }
    return(
        <div className="mobile_bio">
            <div ref={hideBlock} className="hide_window" style={{display: 'flex'}}></div>
        <div ref={iteme} className="login_window">
        <div className="container">
                          
            <i ref={closer} onClick={()=> move(true, iteme, hideBlock)} className="fas close_login fa-times"></i>
            <h2 className="login_lable">Log in</h2>
            <input ref={email} type="text" className="login_in bio_inputs" placeholder="Login"/>
            <input ref={pass} type="text" className="password_in" placeholder="Password"/>
            <div className="reg_active" onClick={()=>setRegister(true)}>do not have account</div>
            <div className="login_buttoms">
            <Link to='/general' className="log_but" onClick={()=> Reg()}>Enter</Link>
            </div>
        </div>
        </div> </div>
    )
}
export default MobileLogin