import { useContext, useRef } from "react";
import Context from "../../Context/Context";
import a from '../../imgs/a'

function Post(){
    const {item, setCurrentPerson, usersDate} = useContext(Context)
    function openProfile (){
        let user
        usersDate.forEach(el=>{
            if( el.id === item.id){
                user = el
            }
        })
        setCurrentPerson(user)

    }
    if(typeof (item) != 'string'){
        return(
            <div className="post" id={item.id}>
            <div className="post_body">
                    <div className="person_img">
                        <img src={require(`../../${item.imgUrl}`)} id={item.imgUrl} onClick={()=> openProfile()} className="post_img" alt=""/>
                    </div>
                    <div className="person_post">
                        <div className="person_name">{item.postName}<div className="person_post_date">{item.postDate}</div>
                        </div>
                        <div className="person_content">
                            {item.postContent}
                        </div>
                    </div>
                </div>
                </div>
        )
    }
    else{
        return(
            <div className="post_time">
                {item}
            </div>
        )
            
    }
    
}
export default Post