import React, { useContext } from 'react'
import Context from '../../Context/Context'

function Tread() {
  const {currentUser, usersDate, user} = useContext(Context)
  let treadClass = (function () {
    if (user.id != currentUser.id) return 'friend'
    else return 'user'
}())

  if(typeof (user) != 'string'){
    return (
      <div className={`tread_block ${treadClass}`}>
        <div className="person_tread">
                <div className="tread_info">
                    <div className="tread_name">{user.postName}</div>
                    <div className="tread_time">{user.postDate}</div>
                </div>
                <div className="tread_content">
                {user.postContent}
              </div>
            </div>
          </div>
    )
  }
  else{
    return(<div className="post_time">
      {user}
    </div>)
    
  }
  
}

export default Tread