import { useContext } from "react"
import Context from "../../Context/Context"
import Post from "./Post";


function News(){
    const {postsDate, isPosts, setCurrentPerson, usersDate} = useContext(Context)
    if(isPosts){
    return(
        <div className="news_main">
        {postsDate.map(item=>{
            return(
            item.map(item=>{
                return(
                    <Context.Provider key={Math.random()} value={{item, setCurrentPerson, usersDate}}>
                <Post />
                </Context.Provider>
                )
            })
            )
        })}
    </div>
    )
    }
    else{
        return(
            <div className="news_main">
                <h1>Loading...</h1>
                </div>)
    }
        
    
}
export default News