import React, { useContext } from 'react'
import Context from '../../Context/Context';

function ExBut (props){
    const {setCurrentUser, setIsTrade, setIsReg, setCurrentPerson, currentPerson} = useContext(Context)
    let exiteCl = `exite_but_user`;
    let exiteText = `Close`
    if (!currentPerson.id) {
        exiteCl = `exite_but`;
        exiteText = `Exite`;
    }
    function Exite(e){
        if(e.target.textContent == `Exite`){
            setCurrentUser({})
            setIsTrade(false);
            setIsReg(false)
        }
        else{
            setCurrentPerson({})
        }
        console.dir(e.target);
    }
  return (
    <>
    <div className={exiteCl} onClick={ e => Exite(e)}>{exiteText}</div>
    </>
  )
}
export default ExBut