import React from 'react'
import { useContext } from "react"
import Context from "../../Context/Context"
import TreadBl from './TreadBl'

function TreadsBlock() {
    const {postsDate, isPosts, setCurrentPerson, usersDate, currentUser, currentPerson, setIsTrade} = useContext(Context)

    function ChoseTread(id){
        usersDate.forEach(item=>{
            if(item.id == id){
                setIsTrade(true)
                setCurrentPerson(item)
                console.log(item);
            }
        })
    }

  return (
    <div className="tread_main">
      {currentUser.friends.map(user=>{
        return (
          <Context.Provider key={Math.random()} value={{user, currentUser, usersDate, ChoseTread}}>
              <TreadBl/>
          </Context.Provider>
        )
      })}
      
    </div>
  )
}

export default TreadsBlock