
import BioContacts from "../Contacts/BioContacts"
import Button from "../Button/Button"
import Header from "../Header/Header"
import Img from "../Img/Img"
import React, { useContext, useEffect, useRef } from "react"
import Context from "../../Context/Context"
import ExBut from "../ExBut/ExBut"

function Bio() {
    const {currentUser, usersDate, setPostsDate, setCurrentUser, currentPerson, setIsContacts, setUsersDate, setIsTrade, setIsReg, setCurrentPerson, screenWidth} = useContext(Context)
    let isFriend = 'Write';
    if(!currentUser.friends.includes(currentPerson.id) && currentPerson.id != currentUser.id && currentPerson.id){
        isFriend = 'Add friend'
    }

    let item
    currentPerson.id ? item = currentPerson: item = currentUser
    return(
        <div className="bio">
            <div className="container">
                <Img src={item.img} screenWidth={screenWidth}/>
                <div className="bio_main">
                <Header name={item.name}/>
                <Context.Provider value={{usersDate, setUsersDate, currentPerson, currentUser, setIsTrade, screenWidth}} >
                <Button item={item} friend={isFriend} usersDate={usersDate}/>
                </Context.Provider>
                <BioContacts args={item} screenWidth={screenWidth}/>
                </div>
                <Context.Provider value={{setCurrentUser, setIsTrade, setIsReg, setCurrentPerson,currentPerson, screenWidth}}>
                <ExBut user={currentUser} person={currentPerson}/>
                </Context.Provider>
            </div>
        </div>
    )
}

export default Bio