import { useContext } from "react"
import Context from "../../Context/Context"

function Header(){
    const {setIsContacts} = useContext(Context)
    return(
        <div className="news_header">
        <div className="menu_open" onClick={()=> setIsContacts(true)}>
            Menu
        </div>
            Posts
        </div>
    )
}
export default Header