import { useContext } from "react"
import Context from "../Context/Context"
import Header from "./Header/Header"
import News from "./News/News"
import Poster from "./Poster/Poster"
import Treads from "./Treads/Treads"
import TreadHeader from "./Header/TreadHeader"

function Main(){
    const {postsDate, isPosts, currentUser, setPostsDate, setIsContacts, setCurrentPerson, usersDate, isTrade, currentPerson, setUsersDate, setIsTrade} = useContext(Context)
    if(currentUser.id){
    return(
    <div className="news">
        <div className="container_news">
            <Context.Provider value={{setIsContacts, currentPerson, setIsTrade, setUsersDate}}>
            <Header/>
            </Context.Provider>
            <Context.Provider value={{postsDate, isPosts, setCurrentPerson, usersDate, currentUser, currentPerson}}>
            <News></News>
            </Context.Provider>
            <Context.Provider value={{currentUser, setPostsDate, postsDate, isTrade, currentPerson, usersDate, setUsersDate}}>
            <Poster/>
            </Context.Provider>
        </div>
    </div>
        )
    }
}
export default Main