import { useEffect, useState } from 'react';
import './App.css';
import Treads from './pages/Treads';
import General from './pages/General';
import React from 'react';
import Context from './Context/Context';
import getUsers from './hooks/getUsers';
import Registrate from './pages/Registrate';
import {Routes, Route, HashRouter} from 'react-router-dom'
import TreadsBl from './pages/TreadsBl';








function App() {
  let sizes = [300, 500, 700, 1000, 1600, 2560];
  let [screenWidth, setScreenWidth] = useState(window.screen.width)
  let [currentUser, setCurrentUser] = React.useState([])
  let [mobileBio, setMobileBio] = useState('none')
  let [currentPerson, setCurrentPerson] = useState({})
  let [isTrade, setIsTrade] = useState(false)
  let [isPosts, setIsPosts] = React.useState(true)
  let [postsDate, setPostsDate] = React.useState([])
  let [isContacts, setIsContacts] = React.useState(false)
  let [currentItem, setCurrentItem] = useState({loging: '', hiden: ''})
  let [usersDate, setUsersDate] = React.useState([])
  let [isReg, setIsReg] = React.useState(false)

  window.addEventListener('load', ()=>{
    if(window.location.pathname != "/" && isReg == false){
      window.location.pathname = '/'
    }
  })

  window.addEventListener('unload', ()=>{
      window.location.pathname = '/'
  })


  window.addEventListener('beforeunload', (e) => {
    e.preventDefault();
    window.location.pathname = '/'
 });
console.log(window.location.pathname);
  


  useEffect(()=>{
    getUsers(setUsersDate)
    
  }, [])


  return(
    
      <Context.Provider value={{sizes, screenWidth, setCurrentUser, setMobileBio, setCurrentPerson, setIsTrade, setIsPosts, setPostsDate, setIsContacts, setCurrentItem, setUsersDate, currentUser, mobileBio, currentPerson, isTrade, isPosts, postsDate, isContacts, currentItem, usersDate, isReg, setIsReg}}>
    <Routes>
      <Route path='/' element={<Registrate/>}/>
      <Route path='/treads' element={<Treads/>}/>
      <Route path='/general' element={<General/>}/>
      <Route path='/treadsBl' element={<TreadsBl/>}/>


        </Routes>
        </Context.Provider>
    
  )
}
export default App;
