import { useContext } from "react"
import Context from "../../Context/Context"
import a from "../../imgs/a";
import { Link, useNavigate } from "react-router-dom";

function TreadBl() {
    const {user, currentUser, usersDate, ChoseTread} = useContext(Context)

    let current;
    usersDate.forEach(item=>{
        if(item.id == user){
            current = item
        } 
    })
    let lastMess = currentUser.treads[user][currentUser.treads[user].length-1]
    console.log(lastMess);
  return (
      
        <Link to={'/treads'} onClick={()=> ChoseTread(user)} id={user} className="tread_item" >
            <div ><img src={require(`../../${current.img}`)} className="tread_item_img"  alt=""/> </div>
            <div className="tread_item_main">
            <div className="tread_item_name">{current.name}</div>
            <div className="last_message">{lastMess.postContent}</div>
            </div>
        </Link>
        
  )
}
export default TreadBl