
import React, { useContext, useEffect, useRef } from "react"
import Context from "../Context/Context"
import MobileBio from "./BioBloks/MobileBio"
import BioPc from "./BioBloks/BioPc"


function Bio(){
    
    const {setCurrentUser, currentUser, setUsersDate, usersDate, setPostsDate, setIsPosts, setCurrentItem, currentItem, currentPerson, setIsContacts, setIsTrade, mobileBio, setMobileBio, setCurrentPerson, screenWidth, isReg, setIsReg} = useContext(Context)
 


    
    if(isReg && currentPerson.postName){
        return(
            <>
            <Context.Provider value={{currentUser, usersDate, setPostsDate, setCurrentUser, currentPerson, setIsContacts, setUsersDate, setIsTrade, mobileBio, setMobileBio, setIsReg, setCurrentPerson, screenWidth}}>
            {screenWidth > 500 ? <BioPc/> : <MobileBio/>}
            </Context.Provider>
            </>
        )
    }
  
    else if(currentUser.name){
        return(
            <>
            <Context.Provider value={{currentUser, usersDate, setPostsDate, setCurrentUser, currentPerson, setIsContacts, setUsersDate, setIsTrade, mobileBio, setMobileBio, setIsReg, setCurrentPerson, screenWidth}}>
            {screenWidth > 500 ? <BioPc/> : <MobileBio/>}
            </Context.Provider>
            </>
        )
    }
    
}
export default Bio