import React from 'react'
import Tread from './Tread'
import { useContext } from "react"
import Context from "../../Context/Context"

function Treads() {
    const {postsDate, isPosts, setCurrentPerson, usersDate, currentUser, currentPerson} = useContext(Context)
    if(currentUser.treads[currentPerson.id]){
  return (
    <div className="tread_main">
      {currentUser.treads[currentPerson.id].map(user=>{
        return (
          <Context.Provider key={Math.random()} value={{currentUser, usersDate, user}}>
            <Tread />
          </Context.Provider>
        )
      })}
      
    </div>
  )
    }
}

export default Treads