import Treads from "./Treads/Treads";
import People from "./People/People";
import Friends from "./Friends/Friends";
import Nomad from "./Nomad/Nomad";
import { useContext, useEffect, useRef } from "react";
import Context from "../Context/Context";

function Contacts (){
    const {currentUser, usersDate, isContacts, setIsContacts, currentItem, setCurrentPerson, setMobileBio, screenWidth} = useContext(Context)
    let contacts = useRef()
    useEffect(()=>{
        if(screenWidth <= 500){
            isContacts? contacts.current.style.transform = `translate(0)` : contacts.current.style.transform = `translate(-100%)`
        }
        
    }, [isContacts])
    
    return(
        <div ref={contacts} className="contacts">
            <div className="container_cont">
                <Context.Provider value={{setIsContacts, currentItem, currentUser, setMobileBio, screenWidth}}>
                <Nomad/>
                </Context.Provider>
                <Context.Provider value={{setIsContacts}}>
                <Treads/>
                </Context.Provider>

                <Context.Provider value={{currentUser, usersDate, setCurrentPerson}}>
                <People/>
                </Context.Provider>

                <Context.Provider value={{currentUser, usersDate, setCurrentPerson}}>
                <Friends/>
                </Context.Provider>
                
            </div>
        </div>
    )
}
export default Contacts