import { useContext, useRef } from "react";
import Context from "../../Context/Context";
import addFriend from "../../hooks/addFriend";
import setUsers from "../../hooks/setUsers";
import { Link, useNavigate } from "react-router-dom";

function Button(props){
    let isUser = props.usersDate.includes(props.currentUser)
    const {usersDate, setUsersDate, currentPerson, currentUser, setIsTrade, screenWidth} = useContext(Context)
    const navigate = useNavigate()
    let but = useRef()
    function butClick(){
        if(props.friend === 'Write'){
            setIsTrade(true)
            if(screenWidth < 500)  props.closeBio()
        }
        else{
            addFriend(usersDate, setUsersDate, currentUser, currentPerson, setUsers)
        }
    }

    if(true){
        return(
            <div className="bio_message">
                <Link ref={but} to="/treads" className="add_friend" onClick={()=> butClick()}>{props.friend}</Link>
            </div>
        )
    }
}
export default Button