import BioSingin from './BioSingin/BioSignin'
import BioLogin from './BioLogin/BioLogin';
import React, { useEffect, useRef } from 'react';
import { useState } from 'react';
import { useContext } from 'react';
import Context from '../../Context/Context';
import MobileLogin from './BioLogin/MobileLogin';
import MobileSignin from './BioSingin/MobileSignin';


function BioRegistratee(data){
    const {setCurrentUser, setIsReg, setUsersDate, usersDate, setPostsDate, setIsPosts, setCurrentItem, currentItem, screenWidth} = useContext(Context)
    let [register, setRegister] = React.useState(false)

    const iteme = useRef()
    
  
   
    
    if(screenWidth > 500){
        return register ?
        <div className="register_bio">
        <Context.Provider value={{setRegister, setUsersDate, usersDate}}>
         <BioSingin data={usersDate}/>
         </Context.Provider> </div> :
         <div className="register_bio"> <Context.Provider value={{setRegister, setIsReg, setCurrentUser, usersDate, setPostsDate, setIsPosts}}>
         <BioLogin data={usersDate}/>
         </Context.Provider>
         </div>
    }
    else{
        return register ? 
        
        <Context.Provider value={{setRegister, setUsersDate, usersDate, iteme, setCurrentItem, currentItem}}>
         <MobileSignin/>
        </Context.Provider>
        :
         <Context.Provider value={{setRegister, setCurrentUser, usersDate, setPostsDate, setIsReg,  iteme, register, setIsPosts, setCurrentItem, currentItem}}>
         <MobileLogin/>
         </Context.Provider>
        
    }
    
            
}
export default BioRegistratee