import BioContacts from "../Contacts/BioContacts"
import Button from "../Button/Button"
import Header from "../Header/Header"
import Img from "../Img/Img"
import React, { useContext, useEffect, useRef} from "react"
import Context from "../../Context/Context"
function MobileBio() {
    const {currentUser, usersDate, setPostsDate, setCurrentUser, currentPerson, setIsContacts, setUsersDate, setIsTrade, mobileBio, setMobileBio, setIsReg, setCurrentPerson, screenWidth} = useContext(Context)
    let item
    currentPerson.id ? item = currentPerson: item = currentUser
    let mobile_bio = useRef()
    let close_but = useRef()

    let isFriend = 'Write';
    if(!currentUser.friends.includes(currentPerson.id) && currentPerson.id != currentUser.id && currentPerson.id){
        isFriend = 'Add friend'
    }

    useEffect(()=>{
        setMobileBio('flex')
        setIsContacts(false)
    }, [currentPerson]);

    function closeBio(){
        setMobileBio('none')
    }
    function exiteBio(){
        if(!currentPerson.id){
            setPostsDate([])
            closeBio()
            setCurrentUser('');
            setMobileBio('none')
        }
        else setCurrentPerson({})
        
    }
    let exiteText = `Close`
    if (!currentPerson.id) exiteText = `Exite`;
    

    return(
        <div ref={mobile_bio} className="mobile_bio" style={{display: mobileBio, top: 0}}>
            <div className="container_cont_m">
            <i ref={close_but} onClick={()=> closeBio()} className="fas close_mobile_bio fa-times"></i>
                <Img src={item.img} screenWidth={screenWidth}/>
                <div className="mobile_bio_main">
                <Header name={item.name}/>
                <Context.Provider value={{usersDate, setUsersDate, currentPerson, currentUser, setIsTrade, screenWidth}} >
                <Button item={item} friend={isFriend} usersDate={usersDate} closeBio={closeBio}/>
                </Context.Provider>
                <BioContacts args={item} screenWidth={screenWidth}/>
                <div className="exite_but" onClick={()=> exiteBio()}>{exiteText}</div>
                </div>
            </div>
        </div>
    )
}

export default MobileBio