
import { useContext, useEffect, useState } from 'react';
import '../App.css';
import Bio from '../Bio/Bio';
import Contacts from '../Contacts/Contacts';
import Main from '../Main/Main';
import React from 'react';
import Context from '../Context/Context';
import getUsers from '../hooks/getUsers';
import { useNavigate } from 'react-router';





function General() {
  const {sizes, screenWidth, setCurrentUser, setMobileBio, setCurrentPerson, setIsTrade, setIsPosts, setPostsDate, setIsContacts, setCurrentItem, setUsersDate, currentUser, mobileBio, currentPerson, isTrade, isPosts, postsDate, isContacts, currentItem, usersDate, isReg, setIsReg} = useContext(Context)
  const navigate = useNavigate()
 useEffect(()=>{
  if(window.location.pathname == '/general') setCurrentPerson({})
 }, [])

 window.addEventListener('load', ()=>{
    window.location.pathname = '/'
    
  console.log(window.location.pathname);
})

window.addEventListener('unload', ()=>{
    window.location.pathname = '/'
    navigate('/')
})
console.log(window.location.pathname);

  return (
    <>
      <Context.Provider value={{currentUser, usersDate, isContacts, setIsContacts, currentItem, setCurrentPerson, setMobileBio, screenWidth}}>
        <Contacts/>
      </Context.Provider>


      <Context.Provider value={{postsDate, isPosts, currentUser, setPostsDate, setIsContacts, setCurrentPerson, usersDate, isTrade, currentPerson, setUsersDate, setIsTrade, screenWidth}}>
        <Main/>
      </Context.Provider>


      <Context.Provider value={{setCurrentUser, currentUser, setUsersDate, usersDate, setPostsDate, setIsPosts, setCurrentItem, currentItem, currentPerson, setIsContacts, setIsTrade, mobileBio, setMobileBio, setCurrentPerson, screenWidth, isReg, setIsReg}}>
        <Bio />
      </Context.Provider>
          </>
  );
}

export default General;
